
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.placeBox {
	border: 1px solid #e6e6e6;
	border-radius: 4px;
	text-align: left;
	padding: 20px 12px;
}
.chooseClass{
	background: #007aff;
	color: #FFFFFF;
}
.placeBox span {
	display: inline-block;
	padding: 10px;
	border: 1px solid #E6E6E6;
	margin-left: 4px;
	margin-top: 4px;
	-webkit-transition: all .5s;
	transition: all .5s;
}
.btn{
	padding: 10px;
	border: none;
	outline: none;
	background: red;
	border-radius: 4px;
	color: white;
	margin-top: 20px;
}
.input{
	height: 34px;
	border: 1px solid #E6E6E6;
	border-radius: 4px;
	margin-top: 20px;
}
